/* # General styles
================================================== */
* {
  outline: none !important;
}
html,
body {
  height: 100%;
}
body {
  font-family: "Open Sans", "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #555;
  font-size: 13px;
}
/* # Layout
================================================== */
.container {
  max-width: none !important;
  padding: 0 20px;
}
.row-no-margin {
  margin-left: 0 !important;
  margin-right: 0 !important;
}
.col-no-margin {
  margin-left: -15px !important;
  margin-right: -15px !important;
}
#container {
  position: relative;
  height: 100%;
}
#container.fixed-header {
  margin-top: 52px;
}
#container > #content > .container {
  float: left;
  width: 100%;
}
#content {
  background: #fff;
  margin-left: 250px;
  overflow: visible;
  padding-bottom: 30px;
  min-height: 100%;
}
#content > .wrapper {
  -webkit-transition: margin ease-in-out 0.1s;
  -moz-transition: margin ease-in-out 0.1s;
  -o-transition: margin ease-in-out 0.1s;
  transition: margin ease-in-out 0.1s;
  position: relative;
}
.sidebar-closed > #content {
  margin-left: 0;
}
/* * * * * * * * * * * *
 * Page Header
 * * * * * * * * * * * */
.page-header {
  border: 0;
  margin: 0;
  *zoom: 1;
}
.page-header:before,
.page-header:after {
  content: " ";
  display: table;
}
.page-header:after {
  clear: both;
}
.page-header:before,
.page-header:after {
  content: " ";
  display: table;
}
.page-header:after {
  clear: both;
}
.page-header:before,
.page-header:after {
  display: table;
  content: "";
  line-height: 0;
}
.page-header:after {
  clear: both;
}
.page-header:before,
.page-header:after {
  display: table;
  content: "";
  line-height: 0;
}
.page-header:after {
  clear: both;
}
.page-title {
  float: left;
  padding: 25px 0;
}
.page-title h3 {
  margin: 0;
  margin-bottom: 10px;
  color: #555;
  font-weight: 400;
  font-size: 20px;
}
.page-title span {
  display: block;
  font-size: 11px;
  color: #888888;
  font-weight: normal;
}
.page-stats {
  float: right;
  padding: 25px 0;
  margin-left: 0;
  list-style: none;
}
.page-stats li {
  float: left;
  display: block;
  margin-left: 20px;
  padding-left: 20px;
  border-left: 1px solid #d9d9d9;
}
.page-stats li:first-child {
  border: 0;
}
.page-stats li .summary {
  float: left;
  margin-right: 18px;
}
.page-stats li .summary span {
  color: #888888;
  font-weight: 600;
  font-size: 11px;
  text-transform: uppercase;
}
.page-stats li .summary h3 {
  color: #555;
  margin: 0;
  font-size: 20px;
  line-height: 20px;
  font-weight: 600;
}
.page-stats li .graph {
  float: right;
}
.page-stats li .sparkline {
  margin-top: 4px;
}
.align-left {
  text-align: left;
}
.align-center {
  text-align: center;
  margin: 0 auto;
}
.align-right {
  text-align: right;
}
/* * * * * * * * * * * *
 * General
 * * * * * * * * * * * */
.full-width-fix {
  width: 100% !important;
}
.row-bg {
  background-color: #f9f9f9;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  margin: 0 -20px !important;
  margin-bottom: 25px !important;
  padding-left: 5px;
  padding-right: 5px;
  padding-top: 25px;
  -webkit-box-shadow: 0 0 1px rgba(0, 0, 0, 0.15) inset;
  -moz-box-shadow: 0 0 1px rgba(0, 0, 0, 0.15) inset;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.15) inset;
}
.hide-default {
  display: none;
}
