.table .progress {
  margin-bottom: 0;
}
/* # Charts
================================================== */
.chart {
  height: 250px;
  z-index: 90;
  width: 100%;
  overflow: hidden;
  /* Sizes */
}
.chart.chart-small {
  height: 100px;
}
.chart.chart-medium {
  height: 200px;
}
.chart.chart-normal {
  height: 250px;
}
.chart.chart-large {
  height: 400px;
}
/* * * * * * * * * * * *
